async function fetchCurrentWeather() {
  const response = await fetch('http://api.weatherapi.com/v1/forecast.json?key=73b76e4355924104b6c103812232611&q=madrid', {mode: 'cors'});
  if (response.ok) {
    return response.json();
  }
  throw new Error ('Location not found');  
}

async function processData() {
  const result = await fetchCurrentWeather();
  console.log(result);
  return {
    currentTemp: result.current.temp_c,
    currentFeelsLikeTemp: result.current.feelslike_c,
    currentCondition: result.current.condition.text,
    currentHumidity: result.current.humidity
  }
}

function displayWeather() {
  processData()
  .then(result => {
      console.log('Current Temperature', result.currentTemp);
      console.log('Temperature feels like', result.currentFeelsLikeTemp);
      console.log('Condition', result.currentCondition);
      console.log('Humidity', result.currentHumidity);
  })
}
displayWeather()